import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

// Отримання посилань на елементи DOM
const btnStart = document.querySelector('button[data-start]');
const input = document.querySelector('#datetime-picker');
const dayEl = document.querySelector('span[data-days]');
const hourEl = document.querySelector('span[data-hours]');
const minuteEl = document.querySelector('span[data-minutes]');
const secondEl = document.querySelector('span[data-seconds]');
const animatedDiv = document.querySelector('#animatedDiv');

// Ініціалізація змінних
let targetDate = null;
let timerInterval = null;

// Функція конвертації мілісекунд у дні, години, хвилини та секунди
function convertMs(ms) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  const days = Math.floor(ms / day);
  const hours = Math.floor((ms % day) / hour);
  const minutes = Math.floor((ms % hour) / minute);
  const seconds = Math.floor((ms % minute) / second);

  return { days, hours, minutes, seconds };
}

// Функція додавання провідних нулів
const addLeadingZero = value => String(value).padStart(2, '0');

// Налаштування flatpickr
const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    const selectedDate = selectedDates[0];
    const now = new Date();

    if (selectedDate <= now) {
      btnStart.disabled = true;

      iziToast.error({
        title: 'Помилка',
        message: 'Будь ласка, виберіть дату в майбутньому',
      });
    } else {
      btnStart.disabled = false;

      iziToast.success({
        title: 'Вдалий вибір',
        message: 'Ви можете натиснути "Start"!',
      });

      targetDate = selectedDate;
    }
  },
};

flatpickr(input, options);

// Обробник натискання кнопки "Start"
btnStart.addEventListener('click', startTimer);

function startTimer() {
  if (!targetDate) {
    iziToast.error({
      title: 'Помилка',
      message: 'Будь ласка, виберіть дату перед запуском таймера',
    });
    return;
  }

  btnStart.disabled = true;
  input.disabled = true;

  // Додавання анімації
  if (animatedDiv) {
    animatedDiv.classList.add('animated');
  }

  // Запобігання множинним інтервалам
  if (timerInterval) {
    clearInterval(timerInterval);
  }

  timerInterval = setInterval(() => {
    const currentDate = new Date();
    const timeDiff = targetDate - currentDate;

    if (timeDiff <= 0) {
      clearInterval(timerInterval);
      timerInterval = null;
      updateTimerDisplay(0, 0, 0, 0);
      btnStart.disabled = false;
      input.disabled = false;

      if (animatedDiv) {
        animatedDiv.classList.remove('animated');
      }

      iziToast.info({
        title: 'Таймер завершено',
        message: 'Час вичерпано!',
      });

      return;
    }

    const { days, hours, minutes, seconds } = convertMs(timeDiff);
    updateTimerDisplay(days, hours, minutes, seconds);
  }, 1000);
}

// Функція оновлення відображення таймера
function updateTimerDisplay(days, hours, minutes, seconds) {
  dayEl.textContent = addLeadingZero(days);
  hourEl.textContent = addLeadingZero(hours);
  minuteEl.textContent = addLeadingZero(minutes);
  secondEl.textContent = addLeadingZero(seconds);
}